/* Mobile enhancements */
@media (max-width: 640px) {
  .app-section {
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
  }
  
  .card {
    margin-left: 0.5rem;
    margin-right: 0.5rem;
  }
  
  .section-title {
    font-size: 1.5rem;
    line-height: 2rem;
  }
  
  .btn-primary, .btn-secondary {
    padding-left: 0.75rem;
    padding-right: 0.75rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    font-size: 0.875rem;
    line-height: 1.25rem;
  }
  
  table {
    font-size: 0.875rem;
    line-height: 1.25rem;
  }
  
  th, td {
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
  }
}
/* Animation effects */
.fade-in {
  animation: fadeIn 0.5s ease-in;
}
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}
/* Subtle hover effects */
.hover-lift {
  transition: transform 0.2s ease;
}
.hover-lift:hover {
  transform: translateY(-4px);
}
/* Better table responsiveness */
.table-container {
  margin-left: -1rem;
  margin-right: -1rem;
